.hero {
  background-color: rgb(18, 18, 18);
  color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.hero-content {
  display: flex;
  flex-direction: column;
  margin-top: -100px;
}

.hero-intro {
  font-size: 2rem;
  color: #00aaff;
  text-align: left;
}

.hero-name {
  font-size: 7rem;
  margin: -1.5rem 0;
}

.hero-tagline {
  font-size: 1.5rem;
  color: #ccc;
  text-align: left;
}

.social-icons {
  margin-top: 1.5rem;
  text-align: left;
}

.social-icons a {
  display: inline-block;
  margin-right: 1rem;
  width: 40px;
  height: 40px;
}

.social-icons img {
  width: 100%;
  height: auto;
  border-radius: 50%;
  transition: transform 0.3s;
}

.social-icons img:hover {
  transform: scale(1.1);
}

.arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 50px;
  height: 50px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.3s;
}

.arrow:hover {
  background-color: rgba(255, 255, 255, 0.8);
}

.arrow-right {
  right: 15px;
}

.arrow img {
  width: 20px;
  height: 20px;
}

/* 響應式設計 */
@media (min-width: 768px) and (max-width: 1024px) {

  .hero-intro {
    font-size: 5vw;
  }

  .hero-name {
    font-size: 12vw;
  }

  .hero-tagline {
    font-size: 4vw;
  }

  .social-icons img {
    width: 5vw;
  }

  .arrow {
    display: none !important;
  }
}

@media (max-width: 768px) {

  .hero-content {
    margin-top: -200px;
  }

  .hero-intro {
    font-size: 5vw;
  }

  .hero-name {
    font-size: 15vw;
  }

  .hero-tagline {
    font-size: 4vw;
  }

  .social-icons img {
    width: 11vw;
  }

  .arrow {
    display: none !important;
  }
}